import { parseHTML } from "../src/parser.js";
import test from "ava";

test("parse a single element", (t) => {
  let div = parseHTML("<div></div>");
  if (div.tagName == "div") {
    t.pass();
  }

  if (div.children.length === 0) {
    t.pass();
  }

  if (div.type === "element") {
    t.pass();
  }

  if (div.attributes.length === 0) {
    t.pass();
  }
  // t.pass();
});
